"use client";

import { Canvas } from "@react-three/fiber";
import { Environment, Float, MeshDistortMaterial, PerspectiveCamera } from "@react-three/drei";
import { Suspense } from "react";

function FloatingShapes() {
    return (
        <>
            {/* Main Organic Shape */}
            <Float
                speed={1.5} // Animation speed
                rotationIntensity={1} // XYZ rotation intensity
                floatIntensity={2} // Up/down float intensity
            >
                <mesh position={[0, 0, 0]} scale={2.5}>
                    <sphereGeometry args={[1, 64, 64]} />
                    <MeshDistortMaterial
                        color="#CBBFB1"
                        attach="material"
                        distort={0.4} // Strength, 0 disables the effect (default=1)
                        speed={1.5} // Speed (default=1)
                        roughness={0.2}
                        metalness={0.8}
                    />
                </mesh>
            </Float>

            {/* Accenting Torus */}
            <Float speed={2} rotationIntensity={2} floatIntensity={1.5} position={[3, 1, -2]}>
                <mesh scale={1.5}>
                    <torusGeometry args={[1, 0.3, 16, 100]} />
                    <meshPhysicalMaterial
                        color="#3F3127"
                        roughness={0}
                        metalness={0.5}
                        transmission={0.5} // Glass-like
                        thickness={2}
                    />
                </mesh>
            </Float>

            {/* Floating Gem */}
            <Float speed={1} rotationIntensity={1.5} floatIntensity={2} position={[-3, -1, 1]}>
                <mesh scale={1.2}>
                    <icosahedronGeometry args={[1, 0]} />
                    <meshStandardMaterial
                        color="#D4AF37"
                        roughness={0.3}
                        metalness={1}
                    />
                </mesh>
            </Float>
        </>
    );
}

export default function Scene3D() {
    return (
        <div className="w-full h-full absolute inset-0 z-0 pointer-events-none">
            <Canvas>
                <PerspectiveCamera makeDefault position={[0, 0, 8]} />
                <ambientLight intensity={0.5} />
                <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} intensity={100} />
                <pointLight position={[-10, -10, -10]} intensity={50} />

                <Suspense fallback={null}>
                    <Environment preset="city" />
                    <FloatingShapes />
                </Suspense>
            </Canvas>
        </div>
    );
}
